<script>
    import axios from 'axios'

    export default {
        name: "Form",
        data: function () {
            return {
                isModalOpen: false,
                isEmailValid: false,
                isCheckboxActive: false,
                isPhoneValid: false,
                email: '',
                phone: '',
            }
        },
        methods: {
            sendForm: function (event) {
                event.preventDefault()
                const data = { email: this.email, phone: this.phone}
                axios
                    .post("/register/", data)
                    .then(response => {})
                    .catch(e => {
                        console.log(e)
                        this.redirect()
                    })
                this.redirect()
            },
            redirect: function () {
                this.$router.push({ name: 'StepTwo' })
            },
            emailValidate: function (event) {
                if (event.currentTarget.validity.valid && event.currentTarget.value !== '') {
                    this.isEmailValid = true
                } else {
                    this.isEmailValid = false
                }
            },
            phoneValidate: function (event) {
                if (event.currentTarget.validity.valid && event.currentTarget.value !== '') {
                    this.isPhoneValid = true
                } else {
                    this.isPhoneValid = false
                }
            },
            validateCheckbox: function (event) {
                if (event.currentTarget.checked) {
                    this.isCheckboxActive = true
                } else {
                    this.isCheckboxActive = false
                }
            }
        }
    };
</script>

