<script>
    import axios from 'axios';
    import Cleave from 'vue-cleave-component';

    export default {
        name: "FormTwo",
        data: function () {
            return {
                isModalOpen: false,
                price: this.$route.params.price + ' ',
                cardMonthYear: '',
                cardNumber: '',
                isCardMonthYearValid: false,
                isCardNumberValid: false,
                isCardCVCValid: false,
                cardCVC: '',
                cardName: '',
                ErrorMSG: '',
                options: {
                    creditCard: true,
                    delimiter: ' ',
                },
                dateOptions: {
                    date: true,
                    datePattern: ['m', 'y']
                },
                cardMonthData: {
                    errorText: ''
                },
                cardNumberData: {
                    errorText: ''
                },
                cardCVCData: {
                    errorText: ''
                },
                cardNameData: {
                    errorText: ''
                }
            }
        },
        components: {
            Cleave
        },
        computed: {
            isCardNameValid() {
                return this.cardName.length > 3;
            }
        },
        methods: {
            sendForm: function () {
                const data = { cardNumber: this.cardNumber, cardMonthYear: this.cardMonthYear, cardCVC: this.cardCVC, cardName: this.cardName }
                axios
                    .post("/payment/", data)
                    .then(response => {
                        if (response.status >= 200 && response.status < 300) {
                            this.redirect();
                        }
                        if (response.status >= 400) {
                            response.paymentMethod.forEach((item) => {
                                this.ErrorMSG += item;
                            });
                            this.cardMonthData.errorText = response.paymentMethod.expiryDate;
                            this.cardNumberData.errorText = response.paymentMethod.pan;
                            this.cardCVCData.errorText = response.paymentMethod.cvv2;
                            this.cardNameData.errorText = response.paymentMethod.holderName;
                        }
                    })
                    .catch(e => {
                        console.log(e)
                    })
            },
            redirect: function () {
                this.$router.push({ name: 'Success' })
            },
            toggleButton: function (event) {
                const oldActive = document.getElementsByClassName('active')[0]
                if (oldActive) oldActive.classList.remove('active')
                event.currentTarget.classList.toggle('active')
            },
            cardNumberValidate: function (event) {
                if (event.currentTarget.validity.valid && event.currentTarget.value !== '') {
                    this.isCardNumberValid = true
                } else {
                    this.isCardNumberValid = false
                }
            },
            cardCVCValidate: function (event) {
                if (event.currentTarget.validity.valid && event.currentTarget.value !== '') {
                    this.isCardCVCValid = true
                } else {
                    this.isCardCVCValid = false
                }
            },
            cardMonthYearValidate: function (event) {
                if (event.currentTarget.validity.valid && event.currentTarget.value !== '') {
                    this.isCardMonthYearValid = true
                } else {
                    this.isCardMonthYearValid = false
                }
            }
        }
    };
</script>

